/** Fonts */
/* philosopher-regular - latin */
@font-face {
  font-family: 'Philosopher';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/Philosopher/philosopher-v19-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('../fonts/Philosopher/philosopher-v19-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/Philosopher/philosopher-v19-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/Philosopher/philosopher-v19-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('../fonts/Philosopher/philosopher-v19-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/Philosopher/philosopher-v19-latin-regular.svg#Philosopher') format('svg'); /* Legacy iOS */
}

